// @flow

import Point from '@mapbox/point-geometry';

import ImageSource from './image_source';

import type Map from '../ui/map';
import type Dispatcher from '../util/dispatcher';
import type {Evented} from '../util/evented';
import type {
    ImageWMSSourceSpecification,
} from '../style-spec/types';

/**
 * A data source containing an image.
 * (See the [Style Specification](https://www.mapbox.com/mapbox-gl-style-spec/#sources-image) for detailed documentation of options.)
 *
 * @example
 * // add to map
 * map.addSource('some id', {
 *    type: 'image',
 *    url: 'https://www.mapbox.com/images/foo.png',
 *    coordinates: [
 *        [-76.54, 39.18],
 *        [-76.52, 39.18],
 *        [-76.52, 39.17],
 *        [-76.54, 39.17]
 *    ]
 * });
 *
 * // update coordinates
 * var mySource = map.getSource('some id');
 * mySource.setCoordinates([
 *     [-76.54335737228394, 39.18579907229748],
 *     [-76.52803659439087, 39.1838364847587],
 *     [-76.5295386314392, 39.17683392507606],
 *     [-76.54520273208618, 39.17876344106642]
 * ]);
 *
 * // update url and coordinates simultaneously
 * mySource.updateImage({
 *    url: 'https://www.mapbox.com/images/bar.png',
 *    coordinates: [
 *        [-76.54335737228394, 39.18579907229748],
 *        [-76.52803659439087, 39.1838364847587],
 *        [-76.5295386314392, 39.17683392507606],
 *        [-76.54520273208618, 39.17876344106642]
 *    ]
 * })
 *
 * map.removeSource('some id');  // remove
 * @see [Add an image](https://www.mapbox.com/mapbox-gl-js/example/image-on-a-map/)
 */
class ImageWMSSource extends ImageSource {
    options: ImageWMSSourceSpecification;
    viewParam: any;

    /**
     * @private
     */
    constructor(id: string, options: ImageWMSSourceSpecification, dispatcher: Dispatcher, eventedParent: Evented) {
        super(id, options, dispatcher, eventedParent);
        this.type = 'image-wms';
        this.options = options;
        this.viewParam = null;
    }

    onAdd(map: Map) {
        this.map = map;
        const _this = this;
        _this.UpdateSourceImage(map.transform.calculateViewParam());
        this.load();
        map.on("moveend", function (e) {
            _this.UpdateSourceImage(map.transform.calculateViewParam());
        })
    }

    serialize(): Object {
        return {
            type: 'image-wms',
            url: this.options.url,
            coordinates: this.coordinates
        };
    }

    hasTransition() {
        return false;
    }

    UpdateSourceImage(param) {
        if (this.viewParam == null ||
            param.width != this.viewParam.width ||
            param.height != this.viewParam.height ||
            param.left != this.viewParam.left ||
            param.right != this.viewParam.right ||
            param.top != this.viewParam.top ||
            param.bottom != this.viewParam.bottom) {
            let coordinates : [[number, number], [number, number], [number, number], [number, number]] = param.coordinates;
            let url:string = this.options.wmsUrl;
            url = url.replace(/{left}/g, param.left);
            url = url.replace(/{top}/g, param.top);
            url = url.replace(/{right}/g, param.right);
            url = url.replace(/{bottom}/g, param.bottom);
            url = url.replace(/{width}/g, param.width);
            url = url.replace(/{height}/g, param.height);
            this.updateImage({
                url,
                coordinates
            })
            this.viewParam = param;
        }

    }

}

export default ImageWMSSource;
