// @flow

import Point from '@mapbox/point-geometry';
import StyleLayer from '../style_layer';

import properties from './raster_style_layer_properties';
import { Transitionable, Transitioning, PossiblyEvaluated } from '../properties';

import type {PaintProps} from './raster_style_layer_properties';
import type {LayerSpecification} from '../../style-spec/types';
import type Map from "../../ui/map";

class WMSRasterStyleLayer extends StyleLayer {
    _transitionablePaint: Transitionable<PaintProps>;
    _transitioningPaint: Transitioning<PaintProps>;
    paint: PossiblyEvaluated<PaintProps>;

    constructor(layer: LayerSpecification) {
        console.log("TCL: WMSRasterStyleLayer -> constructor -> layer", layer)
        super(layer, properties);
        let {coordinates,url,parmsName} = layer.source;
        let {id} = layer;
        // let VisualRangeCoordinates=this.VisualRangeCoordinates();
        // this.coordinates = coordinates instanceof Array ? coordinates : VisualRangeCoordinates;
        this.coordinates = coordinates;
        this.apiUrl = url;
        this.layerId = id;
        this.parmsName = parmsName;
    }

    onAdd(map: Map) {
        const _this = this;
        console.log("TCL: WMSRasterStyleLayer -> onAdd -> _this", _this)
        let coordinates = _this.coordinates;
        map.on("moveend", function (e) {
            let source = map.getSource(_this.layerId);
            let name = Math.floor(Math.random() * 3);
            coordinates = _this.VisualRangeCoordinates(map);

            var transform = map.painter.transform;
            var width = transform.width;
            var height = transform.height;
            var left = _this.NormalToMercatorX(coordinates[0].x);
            var right = _this.NormalToMercatorX(coordinates[0].x);
            var top = _this.NormalToMercatorY(coordinates[0].y);
            var bottom = _this.NormalToMercatorY(coordinates[0].y);
            for (var i = 1; i < coordinates.length; i++) {
                var x = _this.NormalToMercatorX(coordinates[i].x);
                var y = _this.NormalToMercatorY(coordinates[i].y);
                if (left > x) {
                    left = x;
                }
                if (right < x) {
                    right = x;
                }
                if (top < y) {
                    top = y;
                }
                if (bottom > y) {
                    bottom = y;
                }
            }

            var url = source.options.url;
            //var url = `http://cloud.piesat.cn/tile/service/getTileByBounds?left=${left}&top=${top}&right=${right}&bottom=${bottom}&nWidth=${width}&nHeight=${height}`;
            url = url.replace(/{left}/, left);
            url = url.replace(/{top}/, top);
            url = url.replace(/{right}/, right);
            url = url.replace(/{bottom}/, bottom);
            url = url.replace(/{width}/, width);
            url = url.replace(/{height}/, height);
            source.updateImage({
                url,
                coordinates
            })
        })
    }

    NormalToMercatorX(x){
        x = 2 * 20037508.3427892 * (x - 0.5);
        return x;
    }

    NormalToMercatorY(y){
        y = 2 * 20037508.3427892 * (0.5 - y);
        return y;
    }

    VisualRangeCoordinates(map){
        var transform = map.painter.transform;
        var points = [];
        var points0 = transform.pointCoordinate(new Point(0, 0));
        //points0 = this.NormalToMercator(points0);
        points.push(points0);
        var points1 = transform.pointCoordinate(new Point(transform.width, 0));
        //points1 = this.NormalToMercator(points1);
        points.push(points1);
        var points2 = transform.pointCoordinate(new Point(transform.width, transform.height));
        //points2 = this.NormalToMercator(points2);
        points.push(points2);
        var points3 = transform.pointCoordinate(new Point(0, transform.height));
        //points3 = this.NormalToMercator(points3);
        points.push(points3);
        return points;
    }

    FetchAjax(){
        return fetch(_this.apiUrl).then(res=>{
            console.log("TCL: WMSRasterStyleLayer -> onAdd -> res", res);
        })
    }
    onRemove(map: Map) {

    }
}

export default WMSRasterStyleLayer;
