// @flow

import { Event, ErrorEvent, Evented } from '../util/evented';

import { extend, getEPSG, getTileBounds } from '../util/util';
import EXTENT from '../data/extent';

import GeoJSONSource from './geojson_source';
import type Map from '../ui/map';
import type Dispatcher from '../util/dispatcher';
import type {GeoJSON, GeoJSONFeature} from '@mapbox/geojson-types';
import type {GeoJSONWMSSourceSpecification} from '../style-spec/types';

/**
 * A source containing GeoJSON.
 * (See the [Style Specification](https://www.mapbox.com/mapbox-gl-style-spec/#sources-geojson) for detailed documentation of options.)
 *
 * @example
 * map.addSource('some id', {
 *     type: 'geojson',
 *     data: 'https://d2ad6b4ur7yvpq.cloudfront.net/naturalearth-3.3.0/ne_10m_ports.geojson'
 * });
 *
 * @example
 * map.addSource('some id', {
 *    type: 'geojson',
 *    data: {
 *        "type": "FeatureCollection",
 *        "features": [{
 *            "type": "Feature",
 *            "properties": {},
 *            "geometry": {
 *                "type": "Point",
 *                "coordinates": [
 *                    -76.53063297271729,
 *                    39.18174077994108
 *                ]
 *            }
 *        }]
 *    }
 * });
 *
 * @example
 * map.getSource('some id').setData({
 *   "type": "FeatureCollection",
 *   "features": [{
 *       "type": "Feature",
 *       "properties": { "name": "Null Island" },
 *       "geometry": {
 *           "type": "Point",
 *           "coordinates": [ 0, 0 ]
 *       }
 *   }]
 * });
 * @see [Draw GeoJSON points](https://www.mapbox.com/mapbox-gl-js/example/geojson-markers/)
 * @see [Add a GeoJSON line](https://www.mapbox.com/mapbox-gl-js/example/geojson-line/)
 * @see [Create a heatmap from points](https://www.mapbox.com/mapbox-gl-js/example/heatmap/)
 * @see [Create and style clusters](https://www.mapbox.com/mapbox-gl-js/example/cluster/)
 */
class GeoJSONWMSSource extends GeoJSONSource {
    options: GeoJSONWMSSourceSpecification;

    /**
     * @private
     */
    constructor(id: string, options: GeoJSONWMSSourceSpecification & {workerOptions?: any, collectResourceTiming: boolean}, dispatcher: Dispatcher, eventedParent: Evented) {
        super(id, options, dispatcher, eventedParent);
        this.type = 'geojson-wms';
        this.options = options;
        this.viewParam = null;
    }

    onAdd(map: Map) {
        this.map = map;
        this.load();

        const _this = this;
        _this.UpdateSourceData(map.transform.calculateViewParam());
        map.on("moveend", function (e) {
            _this.UpdateSourceData(map.transform.calculateViewParam());
        })
    }

    serialize() {
        return extend({}, this._options, {
            type: this.type,
            data: this._data
        });
    }

    hasTransition() {
        return false;
    }

     UpdateSourceData(param,update = false) {
        if (this.viewParam == null||update||
            param.width != this.viewParam.width ||
            param.height != this.viewParam.height ||
            param.left != this.viewParam.left ||
            param.right != this.viewParam.right ||
            param.top != this.viewParam.top ||
            param.bottom != this.viewParam.bottom
            ) {
            let url:string = this.options.dataUrl;
            let {left , top , right , bottom } = param
            // let points = [`${left},${top}`, `${top},${right}`, `${right},${bottom}`, `${left},${bottom}`, `${left},${top}`]
            let points = [`${left},${top}`, `${right},${top}`, `${right},${bottom}`, `${left},${bottom}`, `${left},${top}`]
            url = url.replace(/{polygon}/, points.join(';'));
            url = url.replace(/{output}/, 'geojson');
            url = url.replace(/{projectionCode}/, '3857');
            this.ajax(url+"#"+new Date().getTime())
            this.viewParam = param;
        }

    }
    ajax(url) {
        var xhr =new XMLHttpRequest()
        let _this = this
        xhr.onreadystatechange = () => {
            if (xhr.readyState == 4) {
                if (xhr.status == 200) {
                    var res = xhr.response;
                    let data = JSON.parse(res).data
                    data.features.map(v=>{
                       if(!v.epsg) v.epsg = 4326
                    })
                    _this.setData(data);
                }
            }
        }
        xhr.open("get", url, true);
        xhr.send();
    }
    refresh(){
        let map = this.map
        this.load();
        const _this = this;
        let parms = map.transform.calculateViewParam()
        _this.UpdateSourceData(parms,true);
    }
}

export default GeoJSONWMSSource;
