// @flow

import Point from '@mapbox/point-geometry';
import CustomStyleLayer from './custom_style_layer';
import type Map from '../../ui/map';
import assert from 'assert';

class SkyStyleLayer extends CustomStyleLayer {

    constructor() {
        this.id = 'SkyDomeLayer';
        this.type = 'custom';
        this.renderingMode = '3d';
    }

    onAdd(map) {

        const vertexSource = `
        attribute vec3 aPos;
        attribute vec4 aColor;
        uniform mat4 uMatrix;

        varying vec4 vColor;

        void main() {
            gl_Position = uMatrix * vec4(aPos, 1.0);
            vColor = aColor;
        }`;

        const fragmentSource = `
        precision mediump float;

        varying vec4 vColor;

        void main() {
            gl_FragColor = vColor;
        }`;

        this.map = map;
        var gl = map.painter.context.gl;
        const vertexShader = gl.createShader(gl.VERTEX_SHADER);
        gl.shaderSource(vertexShader, vertexSource);
        gl.compileShader(vertexShader);
        const fragmentShader = gl.createShader(gl.FRAGMENT_SHADER);
        gl.shaderSource(fragmentShader, fragmentSource);
        gl.compileShader(fragmentShader);

        this.program = gl.createProgram();
        gl.attachShader(this.program, vertexShader);
        gl.attachShader(this.program, fragmentShader);
        gl.linkProgram(this.program);
        gl.validateProgram(this.program);

        this.program.aPos = gl.getAttribLocation(this.program, "aPos");
        this.program.aColor = gl.getAttribLocation(this.program, "aColor");
        this.program.uMatrix = gl.getUniformLocation(this.program, "uMatrix");
    }

    render(gl, matrix) {

        var transform = this.map.painter.transform;
        var curPitch = transform.pitch;
        if (transform.pitch > 60) {
            transform.pitch = 0;
            var coordCenter = transform.pointCoordinate(new Point(transform.width / 2, transform.height / 2));
            var coordFar = transform.pointCoordinate(new Point(transform.width / 2, 0));
            var distance = Math.sqrt((coordFar.x - coordCenter.x) * (coordFar.x - coordCenter.x) + (coordFar.y - coordCenter.y) * (coordFar.y - coordCenter.y));
            transform.pitch = 60;
            var coord = transform.pointCoordinate(new Point(transform.width / 2, 0));
            transform.pitch = curPitch;
            var point = transform.coordinatePoint(coord);
            var coord0 = transform.pointCoordinate(new Point(0, point.y));
            var coord1 = transform.pointCoordinate(new Point(transform.width, point.y));
            var height = distance * Math.sin(curPitch * 0.017453293) * 2;

            const positionArray = new Float32Array([
                coord0.x, coord0.y, 0,
                coord1.x, coord1.y, 0,
                coord0.x, coord0.y, height,
                coord1.x, coord1.y, height]);
            const colorArray = new Float32Array([
                0.8, 0.8, 0.9, 1,
                0.8, 0.8, 0.9, 1,
                0.1, 0.2, 0.7, 1,
                0.1, 0.2, 0.7, 1]);
            const indexArray = new Uint16Array([
                0, 1, 2,
                1, 2, 3
            ]);
            var positionBuffer = gl.createBuffer();
            gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);
            gl.bufferData(gl.ARRAY_BUFFER, positionArray, gl.STATIC_DRAW);
            var colorBuffer = gl.createBuffer();
            gl.bindBuffer(gl.ARRAY_BUFFER, colorBuffer);
            gl.bufferData(gl.ARRAY_BUFFER, colorArray, gl.STATIC_DRAW);
            var indexBuffer = gl.createBuffer();
            gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, indexBuffer);
            gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, indexArray, gl.STATIC_DRAW);

            gl.useProgram(this.program);
            gl.uniformMatrix4fv(this.program.uMatrix, false, matrix);

            gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);
            gl.enableVertexAttribArray(this.program.aPos);
            gl.vertexAttribPointer(this.program.aPos, 3, gl.FLOAT, false, 0, 0);

            gl.bindBuffer(gl.ARRAY_BUFFER, colorBuffer);
            gl.enableVertexAttribArray(this.program.aColor);
            gl.vertexAttribPointer(this.program.aColor, 4, gl.FLOAT, false, 0, 0);

            gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, indexBuffer);
            gl.drawElements(gl.TRIANGLES, 6, gl.UNSIGNED_SHORT, 0);

            gl.deleteBuffer(positionBuffer);
            gl.deleteBuffer(colorBuffer);
            gl.deleteBuffer(indexBuffer);
        }
    }
}

export default SkyStyleLayer;
